.grid-container {
    display: grid;
    /* grid-template-columns: repeat(6, 1fr); */
    grid-template-columns: auto auto auto auto auto auto;
    padding: 10px 10px;
    font-weight: bold;
    font-size: x-large;
    grid-gap: 10px;
  }

  .grid-container-death {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    padding: 10px 10px;
    font-weight: bold;
    font-size: x-large;
    grid-gap: 10px;
  }

  .grid-element {
    /* border-style: solid; */
    border-style: solid;
    /* border-style: outset; */
    border-width: 0.4px;
    /* border-width: 0.01px; */
    box-shadow: 10px 10px 8px #888888;
    /* border-right: 8px solid blue; */
    /* border-bottom: 5px solid blue; */
    padding: 20px 20px;
    text-align: center;
    border-radius: 5px; /* used to be 15 */
  }

  .grid-element:hover {
      box-shadow: 15px 15px 10px #888888;;
  }

  .textStyle {
      font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
      text-decoration: underline;
      font-weight: lighter;
      font-size: x-large;
  }

  .mainText {
    /* border: 10px solid black; */
    padding: 5px 5px;
    background: #333;
    /* background: black; */
    color: #fff;
    text-align: center;
    box-shadow: 2px 2px 1px #888888;
    font-weight: lighter;
    font-size: x-large;
    /* padding: '10px'; */
  }

  .mainText:hover {
    box-shadow: 3px 3px 2px #888888;
  }

  .confirmed {
    border-bottom: 8px solid blue;
  }

  .recovered {
    border-bottom: 8px solid green;
  }

  .death {
    border-bottom: 8px solid red;
  }

  .gridText {
    background: black;
  }